var const_googledinnerandyoutubepanel2018={title:"Google Dinner and YouTube Panel",desc:"An opportunity to connect and learn at 'Google Dinner and YouTube Panel'.",dateyear:"2018",datemonth:"October",datestartday:"19",dateendday:"",time:"6pm - 7:30pm",location:"West Village G, Room 106, Northeastern University",popupContent:'<p>This is a joint event between the Graduate Women Coders and the Google\nUniversity Programs team. Please join us for a networking dinner and panel of\nfemale YouTubers. This will be a wonderful opportunity to ask questions of\nexperienced YouTubers and learn about the possibilities of working at\nYouTube and Google. The panel will be made up of a YouTube Software\nEngineer, a Product Manager, and a Policy Enforcement Manager </p>\n    <p>For any queries, you can reach us at &lt;nugwc-organizers@lists.ccs.neu.edu&gt;</p>\n    '};var const_agiledevopstalk2018={title:"Introduction to AGILE and DEVOPS",desc:"NUGWC is kicking off its first event for Fall 18 this Friday with 'Introduction to AGILE and DEVOPS'.",dateyear:"2018",datemonth:"October",datestartday:"5",dateendday:"",time:"6:30pm-7:30pm",location:"West Village G, Room 106, Northeastern University",popupContent:'<p>Come learn about Agile and Devops culture and why are these the core of working culture for a lot of big compaines. We have an former HSBC employee now Northeastern Masters in Computer Science student, Ritika Gupta touching up on key Agile and Devops concepts and sharing her experience of working with HSBC Global Banking And Markets to help them adopt the same.</p>\n    <p><a href="https://introductiontoagiledevops.splashthat.com/">Check in to the event here</a></p>\n    <p>For any queries, you can reach us at &lt;nugwc-organizers@lists.ccs.neu.edu&gt;</p>\n    '};var const_witempsummit2018={title:"Women in Technology Empowerment Summit",desc:"Calling all STEMINISTS, Entrepreneurs, Designers & Female supporters!! Mark your calendars! 03/23! NUGWC & NUWIT presents: Women in Technology Empowerment Summit 2018!",dateyear:"2018",datemonth:"March",datestartday:"23",dateendday:"",time:"6pm-8pm",location:"West Village G, Room 106, Northeastern University",popupContent:" <p> Calling all STEMINISTS, Entrepreneurs, Designers & Female supporters!! Mark your calendars! 03/23! </p> <p> NUGWC & NUWIT presents: Women in Technology Empowerment Summit 2018!    </p><p>Date: Friday, 23 March 2018 </br>Time: 6 ​PM – 8 PM </br>Location: West Village G, Room 106 </br></p><p>•Are you ready to launch yourself into the real world?</p><p •Do you think you have all it takes to stand out and build your brand in the industry?</p><p>•Would you like to know more about the challenges and tips in this male-dominated field and on how to overcome them?</p><p> •Have you begun to think about your career path in the industry of your choice?</p><p>Join us as we explore these questions and more with our renowned panelists from Harvard University and SAT drones who will provide insights into </br> ~workplace culture</br> ~team collaboration </br>~behavioral skills </br> ~interviewing skills </br>~the most important leadership and technical skills to have ~mentorship </br> ~networking and more! </br></p><p>These tips can also be applied to any major- business, project management, science you name it! 🤩</p><p>You do not want to miss out on this opportunity! 🙅‍♀</p><p> It is open to all undergrads, graduates, and alumni (male and female) Please forward this to all Northeastern Fellows! We hope to see you there!</p> <p>FREE PIZZAS </p>"};var const_cloud9google2018={title:"Cloud 9 with Google",desc:"We are excited to announce our collaboration with Google for our next event: Cloud talks with Google! (GCP) to develop a profound understanding of the platform from top cloud expert.",dateyear:"2018",datemonth:"February",datestartday:"23",dateendday:"",time:"3 ​PM – 5 PM",location:"355 Main Street, Cambridge, MA, 02142",popupContent:'<p><span class="_4n-j _3cht fsl">We are excited to announce our collaboration with Google for our next event: Cloud talks with Google! (GCP) to develop a profound understanding of the platform from top cloud expert</span>.</p>\n    <p><strong>Date:</strong>&nbsp;Friday, 23 February 2018<span class="text_exposed_show"><br /><strong>Time:</strong>&nbsp;3 \u200BPM &ndash; 5 PM<br /><strong>Location:</strong>&nbsp;355 Main Street, Cambridge, MA, 02142<br /></span></p>\n    <p>&nbsp;</p>\n    <p><span class="_4n-j _3cht fsl">The event details are as follows:<br />-45 minute office tour of Google&rsquo;s aesthetic Cambridge office<br />-45 minute panel discussion on GCP<br />-30 minute networking!! (Ask about hiring, interviewing and much more!)</span></p>\n    <p>You don&rsquo;t want to miss this! Availability is limited to 40 students and is RSVP ONLY!!! so grab your spots ASAP!</p>\n    <p><a href="https://docs.google.com/forms/d/e/1FAIpQLSdMZPDCkISH2nzkbnHUevOxOR1C0KESDGNPG85z_-kWYlU7nw/viewform">SIGN UP</a></p>"\n    '};var const_algandintprep2018={title:"Algorithms and Interview Prep",desc:"Whiteboarding skills: You’ll solve algorithms in small groups, breaking off into your preferred programming language and working together to find a solution. Problems are provided and vary in difficulty, check out the Github for examples of past problems: https://github.com/WomenWhoCodeBoston/algorithms/",dateyear:"2018",datemonth:"February",datestartday:"13",dateendday:"",time:"6 ​PM – 8 PM",location:"255 Main St, Cambridge, MA",popupContent:'<p><span class="_4n-j _3cht fsl">Laptops NOT required! This Meetup covers algorithms, where you will have a chance to practice interview questions in a friendly, collaborative environment. We&rsquo;ll split attendees into smaller groups.</span></p>\n    <p><span class="_4n-j _3cht fsl">For Beginners : you&rsquo;ll pair with other early learners and practice answering easier questions. You&rsquo;re also encouraged to join a group and learn from their solutions.</span></p>\n    <p>The maximum capacity is 30 students so RSVP asap to secure your spot! Follow the instructions on the event page to create your account and practice your algorithm solving skills</p>\n    <p><span class="_4n-j _3cht fsl"><a href="http://rsvp%20here:%20https//www.meetup.com/Women-Who-Code-Boston/events/246711550/">RSVP HERE</a></span></p>'};var const_internshiptalks2018={title:"Internship Talks",desc:'NUGWC is kicking off its first event this Friday with\n    \u201CInternship talks\u201D. We\u2019ll have speakers/students who have managed to\n    secure internship opportunities at some of the best companies, to give\n    an encounter of their experiences in the interviews.\n\n    ',dateyear:"2018",datemonth:"February",datestartday:"9",dateendday:"",time:"6 ​PM – 7 PM",location:"ariotis Hall, Room 011",popupContent:'<p>Come learn about acing technical interviews at best companies.</p>\n    <p>FREE PIZZA</p>\n    <p><a href="https://www.facebook.com/events/455766364841834/">Check in to the event here</a></p>\n    <p>For any queries, you can reach us at &lt;nugwc-organizers@lists.ccs.neu.edu&gt;</p>\n    '};var const_sectechtalk2017={title:"Cyber Security Tech Talk",desc:'Our next tech talk speaker is Hashad Sathaye. He is a fellow CCIS grad student interested in Cyber Security. He will be talking about common security issues encountered in web apps and ways of fixing these issues. He will cover two critical vulnerabilities which arise as a result of insecure coding practices, SQL Injection and File Inclusion.\n\n\n    ',dateyear:"2017",datemonth:"November",datestartday:"17",dateendday:"",time:"6 ​PM – 7 PM",location:"Dodge Hall, Room 140",popupContent:'<p>Demos:<br />&ndash; Demo on vulnerabilities of a live web app in a protected environment<br />&ndash; How to patch these vulnerabilities</p>\n    <p>This will be our last tech talk for Fall 2017. Do come and enjoy the tech talk, network and get to know your fellow students. As always, there will be FREE PIZZA.</p>\n    <p>For any queries, you can reach us at &lt;nugwc-organizers@lists.ccs.neu.edu&gt;</p>\n    <p><a href="https://www.facebook.com/events/1743304179309764/">Check into the Event on Facebook!</a></p>\n    '};var const_gmosession2017={title:"GMO’s Information Technology Info Session",desc:'The GMO Information Technology team provides innovative technology solutions to GMO\u2019s clients, traders, portfolio managers, and quantitative analysts. The team provides the technology and support needed to maintain usability of all the systems used across the firm.\n\n    There will be FREE FOOD as always!\n    ',dateyear:"2017",datemonth:"October",datestartday:"10",dateendday:"",time:"4:30 PM – 6:00 PM",location:"129 Hurting Hall, Northeastern University",popupContent:'<p><a href="https://www.facebook.com/events/2178801665679772/?active_tab=about">Check into the Event on Facebook!</a></p>\n    '};var const_talkintuit2017={title:"Tech Talk with Intuit",desc:'Intuit is back on Campus and we have the opportunity to join them for a tech talk on Progressive Web Apps. Two of Intuit\u2019s experienced Software Engineers will be\n    taking us through the What, Why & How of Progressive Web Apps and showing us how we can quickly build one.\n    ',dateyear:"2017",datemonth:"September",datestartday:"29",dateendday:"",time:"6:00 PM – 7:00PM",location:"Room 102, West Village G",popupContent:'<p><strong>Speakers</strong>:<br />Anupriya Haribabu<br />Jyotsna Parthasarathy</p>\n    <p>There will be FREE FOOD as always!</p>\n    <p>Please note, our event capacity is 70 &ndash; 75 people. We have a much bigger room this time to accommodate a lot more people. Room will be occupied on a first come first serve basis.</p>\n    <p>For any queries, you can reach us at &lt;nugwc-organizers@lists.ccs.neu.edu&gt;</p>\n    <p><a href="https://www.facebook.com/events/496946100657664/?acontext=%7B%22ref%22%3A%224%22%2C%22feed_story_type%22%3A%22370%22%2C%22action_history%22%3A%22null%22%7D">Check into the Event on Facebook!</a></p>\n    '};var const_internshiptalks2017={title:"Internship Talks",desc:'This Friday we will have the opportunity to interact with\n    some of the returning interns and hear them speak about their Summer Internships.\n    They will be talking about what projects they worked on, how they bagged the Internship,\n    what the interview process was like and what resources they used for their Interview preparations.\n    As many of us are getting geared up to look for Summer Internship opportunities for 2018, it would be\n    great to get a perspective on what to expect during the process. Do join us and ask lots of questions.\n    ',dateyear:"2017",datemonth:"September",datestartday:"15",dateendday:"",time:"6:00-7:00 PM",location:"Dodge Hall, Room 140",popupContent:'<p><strong>Speakers:</strong><br />Rohan Phadke &ndash; Facebook<br />Navya Kuchibotla &ndash; Amazon<br />Shabbir Hussein &ndash; Google<br />Purva Kamat &ndash; VMware<br />Tanvi Jambhor<span class="text_exposed_show">e &ndash; Novartis<br />Amit Banne &ndash; Pingup<br />Vinayak Shamnani &ndash; IBM</span></p>\n    <p>For any queries, you can reach us at &lt;nugwc-organizers@lists.ccs.neu.edu&gt;</p>'};var const_microservicessummer17={title:"Summer’17 Event 10: Microservices Architecture-Using Docker",desc:'Our next session is happening on Thursday, August 3, from 6:30pm \u2013 7:30pm, at West Village G, R104.\n\n    Join NUGWC to learn about microservices and Docker. As always, we will have Pizza! \uD83D\uDE42\n    ',dateyear:"2017",datemonth:"August",datestartday:"3",dateendday:"",time:"6:30pm – 7:30pm",location:"West Village G, R104",popupContent:'<p>Microservices are the emerging application platform, it is the architecture that will serve as the basis for many applications over the next 10 years. Architecturally, the micro services that containers enable easily are equally groundbreaking. The ability to break a problem into smaller pieces is always beneficial in unexpected ways, and containers offer a way of doing that on a scale not possible before.</p>\n    <p><img class="alignleft" src="https://media.licdn.com/mpr/mpr/shrinknp_400_400/AAEAAQAAAAAAAAVSAAAAJGMyZGZkYjU0LWYwMjgtNDZjYy04MjMyLWRkNjUyNzZhMzVhNg.jpg" /></p>\n    <p><strong>Speaker</strong>:&nbsp;Pranjal Jain, Graduate Student at Northeastern University</p>\n    <p><strong>Speaker bio</strong>:&nbsp;Pranjal is a Graduate Student at Northeastern University and also graduate teaching assistant for Advances in Data Sciences. Experienced full stack developer. Enthusiastic web and big data developer.</p>\n    <p><strong>Website</strong>:&nbsp;<a href="https://jainpranj.github.io/mywebsite/#/about">https://jainpranj.github.io/mywebsite/#/about</a></p>\n    <p><strong>LinkedIn</strong>:&nbsp;<a href="https://www.linkedin.com/in/jainpranj">https://www.linkedin.com/in/jainpranj</a></p>\n    '};var const_codeathonII2017={title:"Summer’17 Event 9: Code-a-thon II",desc:'Join us for our last session of the season. We will get together to solve exciting, yet simple coding challenges on HackerRank.\n\n    Please note, the time and location of this event are not as usual\n    ',dateyear:"2017",datemonth:"July",datestartday:"25",dateendday:"",time:"6:30-8:00 PM",location:"231 Richard’s Hall",popupContent:'<p>Design&nbsp;and analysis of algorithms form the basis of modern computer science. When you think code, it&rsquo;s basically about designing an efficient algorithm to solve a problem.</p>\n    <p>Join together and compete in a coding challenge hosted on HackerRank; the winner gets a Northeastern&nbsp;<strong>goodie</strong>!</p>\n    <p>Hope to see you there! As always, we&rsquo;ll have pizza. Please note again: the venue is not the same as usual.</p>\n    '};var const_codeathon2017={title:"Summer’17 Event 8: Code-a-thon!",desc:'Join us for an exciting tech talk! For our next session, we will get together to solve exciting, yet simple coding challenges on HackerRank.\n\n    Please note, the time and location of this event are not as usual\n    ',dateyear:"2017",datemonth:"July",datestartday:"18",dateendday:"",time:"6:30-8:00 PM",location:"231 Richard’s Hall",popupContent:'<p>The construction and analysis of algorithms form the basis of modern computer science. When you think code, it&rsquo;s basically about designing an efficient algorithm to solve a problem.</p>\n    <p>Join together and compete in an online coding challenge hosted on HackerRank; the winner gets a Northeastern&nbsp;<strong>goodie</strong>!</p>\n    <p>Hope to see you there! As always, we&rsquo;ll have pizza. Please note again: the venue is not the same as usual.</p>\n    '};

